import Vue from "vue";
import VueRouter from "vue-router";
import Main from "../views/Main.vue";
import VoteR from "../views/VoteR.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "main",
    component: Main,
  },
  {
    path: "/voter",
    name: "voter",
    component: VoteR,
  },
];

// eslint-disable-next-line no-new
const router = new VueRouter({
  routes,
});

export default router;
