<template>
  <div class="father">
    <template>
      <el-table
        :data="checkedStudents"
        style="width: 100%"
        :default-sort="{ prop: 'count', order: 'descending' }"
      >
        <el-table-column type="index" sortable width="80"></el-table-column>
        <el-table-column prop="count" label="票数" sortable width="180">
        </el-table-column>
        <el-table-column prop="name" label="姓名" sortable width="180">
        </el-table-column>
        <el-table-column prop="id" label="学号" sortable width="180">
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkedStudents: [],
    };
  },

  methods: {
    async fetch() {
      const res = await this.$http.get("/search");
      this.checkedStudents = res.data;
    },
    formatter(row) {
      return row.address;
    },
    indexMethod(index) {
      return index * 2;
    },
  },
  created() {
    this.fetch();
    setInterval(() => {
      this.fetch();
    }, 1000);
  },
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
}
.father {
  position: relative;
  width: 100%;
  height: fit-content;
}
</style>
